<template>
  <div>
    <el-table
      :data="tableData"
      border
      style="width: 98%;margin:0 auto;">
      <el-table-column
        prop="league_name"
        label="赛事"
        align="center"
        width="300"
        column-key="league_name"
        :filters="filterLeague"
        :filter-method="filterLeagueHandle"
        >
      </el-table-column>
      <el-table-column
        prop="comm_time"
        label="比赛时间"
        align="center"
        width="200">
      </el-table-column>
      <el-table-column
        prop="home_name"
        align="center"
        label="主队">
      </el-table-column>
      <el-table-column
        prop="away_name"
        align="center"
        label="客队">
      </el-table-column>
      <el-table-column
        label="查看"
        align="center"
        width="200"
      >
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="openTo('/asia',scope.row)">亚</el-button>
          <el-button type="text" size="small" @click="openTo('/europe',scope.row)">欧</el-button>
          <el-button type="text" size="small" @click="openTo('/goal',scope.row)">大</el-button>
          <el-button type="text" size="small" @click="openTo('/scroll',scope.row)">滚</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: 'Main',
  data () {
    return {
      intervalId: '',
      tableData: [{
        comm_id: 123
      }],
      filterLeague: []
    }
  },
  mounted () {
    this.initLeague()
    // 循环加载数据
    this.initData()
    clearInterval(this.intervalId)
    this.intervalId = setInterval(() => {
      this.initData()
    }, 2000)
  },
  methods: {
    initData () {
      this.$axios.get('/upcoming/list.json')
        .then(resp => {
          const respData = resp.data
          if (respData.code !== 0) {
            console.log(respData.message)
            return
          }
          this.tableData = respData.result
        })
    },
    initLeague () {
      this.$axios.get('/league/list.json')
        .then(resp => {
          const respData = resp.data
          if (respData.code !== 0) {
            console.log(respData.message)
            return
          }

          const filterList = []
          for (var i in respData.result) {
            filterList.push({
              text: respData.result[i].league_name,
              value: respData.result[i].league_name
            })
          }
          this.filterLeague = filterList
        })
    },
    goTo (target, data) {
      clearInterval(this.intervalId)
      this.$router.push({
        path: target,
        query: {home_name: data.home_name, away_name: data.away_name, comm_id: data.comm_id}
      })
    },
    openTo (taget, data) {
      let routeData = this.$router.resolve({ path: taget, query: {home_name: data.home_name, away_name: data.away_name, comm_id: data.comm_id} })
      window.open(routeData.href, '_blank')
    },
    filterLeagueHandle (value, row, column) {
      const prop = column['property']
      return row[prop] === value
    }
  },
  destroyed () {
    clearInterval(this.intervalId)
  }
}
</script>

<style scoped>
</style>



